import Vue from 'vue'
import Vuex from 'vuex'

const actions = {
	changemohu(context, value) {
		if (value !== '') {
			context.commit('CHANGEMOHU', value)
		}
	},
	changemohu2(context, value) {
		if (value === 0) {
			context.commit('CHANGEMOHU2', value)
		}
	}
}
const mutations = {
	CHANGEMOHU(state, value) {
		state.rsmohu = true
	},
	CHANGEMOHU2(state, value) {
		state.rsmohu = false
	},
	//
	MOHUSOUSUO(state, value) {
		state.rsmohu = value
		console.log(state.rsmohu);
	}
}
const state = {
	//
	list: [{
			image: '/static/img/01.png'
		},
		{
			image: '/static/img/02.png'
		},
		{
			image: '/static/img/03.png'
		}
	],
	//
	rsmohu: ''
}
Vue.use(Vuex)

export default new Vuex.Store({
	actions,
	mutations,
	state
})